<template>
  <div id="app">
    <section>
      <div class="header my-3">
        Fin de la Coding School <span class="text-danger">22</span>
      </div>
      <div class="counter">
        <div class="container mt-2 w-100">
          <div class="row">
            <div class="col-12 col-md-3">
              <div class="d-flex flex-column align-items-center mx-4">
                <p class="mb-0 fs-4">Days</p>
                <h2>{{ days }}</h2>
              </div>
            </div>
            <div class="col-12 col-md-3">
              <div class="d-flex flex-column align-items-center mx-4">
                <p class="mb-0 fs-4">Hours</p>
                <h2>{{ hours }}</h2>
              </div>
            </div>
            <div class="col-12 col-md-3">
              <div class="d-flex flex-column align-items-center mx-4">
                <p class="mb-0 fs-4">Minutes</p>
                <h2>{{ minutes }}</h2>
              </div>
            </div>
            <div class="col-12 col-md-3">
              <div class="d-flex flex-column align-items-center mx-4">
                <p class="mb-0 fs-4">Seconds</p>
                <h2>{{ seconds }}</h2>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      days: Math.floor(
        (new Date("june 17, 2022 17:00:00") - new Date()) /
          (1000 * 60 * 60 * 24)
      ),
      hours: this.formatDate(
        Math.floor(
          ((new Date("june 17, 2022 17:00:00") - new Date()) %
            (1000 * 60 * 60 * 24)) /
            (1000 * 60 * 60)
        )
      ),
      minutes: this.formatDate(
        Math.floor(
          ((new Date("june 17, 2022 17:00:00") - new Date()) %
            (1000 * 60 * 60)) /
            (1000 * 60)
        )
      ),
      seconds: this.formatDate(
        Math.floor(
          ((new Date("june 17, 2022 17:00:00") - new Date()) % (1000 * 60)) /
            1000
        )
      ),
    };
  },
  methods: {
    formatDate: function (date) {
      if (date < 10) return "0" + date;
      if (date >= 10) return date;
    },
    calculFinCoding() {
      setInterval(() => {
        this.days = Math.floor(
          (new Date("june 17, 2022 17:00:00") - new Date()) /
            (1000 * 60 * 60 * 24)
        );
        this.hours = this.formatDate(
          Math.floor(
            ((new Date("june 17, 2022 17:00:00") - new Date()) %
              (1000 * 60 * 60 * 24)) /
              (1000 * 60 * 60)
          )
        );
        this.minutes = this.formatDate(
          Math.floor(
            ((new Date("june 17, 2022 17:00:00") - new Date()) %
              (1000 * 60 * 60)) /
              (1000 * 60)
          )
        );
        this.seconds = this.formatDate(
          Math.floor(
            ((new Date("june 17, 2022 17:00:00") - new Date()) % (1000 * 60)) /
              1000
          )
        );
      }, 1000);
    },
  },
  mounted() {
    this.calculFinCoding();
  },
};
</script>

